@import "../variables/index";

.nav {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  border-bottom: 1px solid $border-color-1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding-right: 50px;

  &-right {
    display: grid;
    grid-template-columns: repeat(3, auto);
    column-gap: 15px;
    flex-shrink: 0;
  }
}

.logo {
  position: relative;
  height: 100%;
  width: 300px;
  flex-shrink: 0;

  &__img {
    object-fit: contain;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.search {
  &-box {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
  }

  &-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    font-size: 24px;
    flex-shrink: 0;
    color: $fontColor3;
  }

  &-input {
    height: 100%;
    width: 100%;
    padding-right: 20px;

    &:focus + .search-icon {
      color: $fontColor2;
    }
  }
}
